.task-wrapper{
  display: flex;
  justify-content: flex-start;
  width: fit-content;
  margin: 10px 0;

  img{
    width: 40px;
    height: 40px;
    min-width: 40px;
    min-height: 40px;

    background-color: #404040;
    border-radius: 50%;
    margin: 5px 5px 0 0;
  }


  .task{
    display: flex;
    justify-content: flex-start;


    .task-inner{
      display: flex;
      max-width: 80%;
      align-items: center;
      height: fit-content;
      box-sizing: border-box;
      padding: 10px;
      --task-bg-color: var(--tg-theme-secondary-bg-color);
      background-color: var(--task-bg-color, #404040);
      border-radius: 15px;



      input{
        min-width: 25px;
        min-height: 25px;
        border-radius: 5px;
        background-color: #404040;
      }
      p{
        height: auto;
        width: 90%;
        text-wrap: normal;
        text-align: start;
        margin: 0;

      }
    }
  }
}