body {
  height: 100vh;
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  --bg-color: var(--tg-theme-bg-color, #fff);
  font-family: sans-serif;
  background-color: var(--bg-color);
  color: var(--tg-theme-text-color, #222);
  font-size: 14px;
  color-scheme: var(--tg-color-scheme);
  font-family: var();


  #root {
    height: 100%;


    .App {
      text-align: center;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      overflow: hidden;

      .wrapper {
        display: flex;
        height: 100%;
        flex-direction: column;
        justify-content: space-between;

        .task-group {
          width: 100%;
          display: flex;
          height: 20px;
          justify-content: space-between;
          button{
            flex-basis: 33%;
            background-color: var(--tg-theme-secondary-bg-color);
            border: 0;
            outline: 0;
            border-radius: 15px;
          }
        }


        & > div:last-child {
          width: 100%;
          height: calc(100% - 75px);
          display: flex;
          flex-direction: column;
          justify-content: space-between;


          .task-list {
            box-sizing: border-box;
            padding: 15px;
            overflow-y: scroll;
            height: calc(100% - 60px);

          }

          .send-message {
            display: flex;
            align-items: center;
            width: 100%;
            height: 50px;
            background-color: #fff;

            form {
              margin: 0;
              width: 100%;
              height: 100%;
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding: 0 10px;

              input {
                margin: 0;
                width: 87%;
                height: 50px;
                padding: 10px;
                box-sizing: border-box;
                border: 0;

                &:focus {
                  border: 0;
                  outline: 0;
                }
              }

              button {
                width: 13%;
                min-width: 93px;
                max-width: 120px;
                height: 30px;
                outline: 0;
                border: 0;
                box-sizing: content-box;
                padding: 0;
                border-radius: 15px;
                background-color: #23b8ff;
              }
            }
          }
        }
      }
    }
  }
}


